import {IMerkleProof} from "../config";

export const useGetUsersProof = (merkleProof: IMerkleProof, address: string | undefined): null | string[] => {
  const proof = merkleProof.leaves.filter((data) => data.data.address.toLowerCase() === address?.toLowerCase());
  console.log('useGetUsersProof', proof);
  if (proof.length === 1) return proof[0].proof;
  else return null;
}
